import React, { useEffect, useState } from "react";
import useFetch_GET from "../../services/http/Get";
import useFetch_POST from "../../services/http/Post";
import "./BuildSolution.css";

function LibraryItem({
  title,
  icon,
  inputPlaceholder,
  action,
  value,
  path,
  pathValue,
}) {
  const [inputValue, setInputValue] = useState("");
  const [isExpanded, setIsExpanded] = useState(false);
  const handleChange = (event) => {
    setInputValue(event.target.value);
  };

  const handleAdd = () => {
    action(value);
    path(pathValue);
    setInputValue("");
  };

  const toggleExpand = () => {
    setIsExpanded(!isExpanded);
  };

  return (
    <div className="library-item">
      <div className="library-item-header" onClick={toggleExpand}>
        <div className="library-item-icon">{icon}</div>
        <div className="library-item-title">{title}</div>
        <div className="library-item-toggle">{isExpanded ? "-" : "+"}</div>
      </div>
      {isExpanded && (
        <div className="library-item-content">
          <div className="library-item-input">
            <div className="input-wrapper">
              <input
                type="text"
                placeholder={inputPlaceholder}
                value={value}
                // onChange={handleChange}
                disabled
              />
            </div>
            <div className="button-wrapper">
              <button onClick={handleAdd} className="add-to-solution-button">
                Add to Solution
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

function BuildSolutionWithLibraries() {
  const {
    isLoading: postLoading,
    errorpostError,
    data: buildSolutionData,
    postData,
  } = useFetch_POST();

  const {
    isLoading: darftLoading,
    error: darftError,
    data: darftData,
    postData: saveDarftData,
  } = useFetch_POST();

  const [fileName, setFileName] = useState(null);
  const [selectedLibraries, setSelectedLibraries] = useState([]);
  const [path, setPath] = useState([]);
  const { isLoading, error, data, getData } = useFetch_GET();
  const handleAddLibrary = (libraryName) => {
    setSelectedLibraries([...selectedLibraries, libraryName]);
  };
  const buildSolution = () => {
    let data = { filePaths: path };
    postData("/v2/build_script", data);
  };
  const handlesetPath = (data) => {
    setPath([...path, data]);
  };

  const handleRemoveLibrary = (index) => {
    setSelectedLibraries(selectedLibraries.filter((_, i) => i !== index));
    setPath(path.filter((_, i) => i !== index));
  };
  const saveToDirectory = () => {
    let data = { content: buildSolutionData?.data, fileName: fileName };
    saveDarftData("/v2/save-script", data);
  };
  useEffect(() => {
    getData("/v2/libraries");
  }, []);
  return (
    <div className="build-solution-with-library">
      <h1>Build Solution with Libraries</h1>

      <div className="library-search-bar">
        <input type="text" placeholder="Search for libraries..." />
      </div>
      <div className="library-list">
        {data?.map((item, index) => (
          <LibraryItem
            key={index}
            title={item.scriptName}
            icon={<i className="fa fa-circle" />}
            inputPlaceholder={item.description}
            value={item.scriptName}
            path={handlesetPath}
            pathValue={item.scriptPath}
            action={handleAddLibrary}
          />
        ))}
      </div>
      <div className="selected-libraries">
        <h2>Selected Libraries ({selectedLibraries.length})</h2>
        <ul>
          {selectedLibraries.map((library, index) => (
            <li key={index}>
              {library}
              <button onClick={() => handleRemoveLibrary(index)}>Remove</button>
            </li>
          ))}
        </ul>
      </div>
      <div style={{ marginTop: "2rem" }}>
        <label>File Name </label>
        <input
          type="text"
          value={fileName}
          onChange={(e) => {
            setFileName(e.target.value);
          }}
        />
      </div>
      <div className="build-review">
        <button
          className="build-button"
          onClick={() => {
            buildSolution();
          }}
        >
          Build
        </button>
        <button className="review-button">Review</button>
      </div>
      <div className="reset-save">
        <button className="reset-button">Reset</button>
        <button
          className="save-button"
          onClick={() => {
            saveToDirectory();
          }}
          disabled={fileName == null ? true : false}
        >
          Save
        </button>
      </div>
      {buildSolutionData && (
        <textarea
          style={{ width: "100%", height: "50vh", marginTop: "5rem" }}
          value={buildSolutionData?.data}
        />
      )}
    </div>
  );
}

export default BuildSolutionWithLibraries;
