import React, { useEffect, useState } from "react";
import useFetch_GET from "../../services/http/Get";
import useFetch_POST from "../../services/http/Post";
import "./BuildSolution.css";

function SolutionRepository({ handleInputChange, setPath }) {
  const { isLoading, error, data, getData } = useFetch_GET();
  let {
    isLoading: postLoading,
    error: postError,
    data: postData_,
    postData,
  } = useFetch_POST();

  const [activeTab, setActiveTab] = useState("all");
  const [activeSolution, setActiveSolution] = useState(null);

  const openTab = (tabName) => {
    setActiveTab(tabName);
    setActiveSolution(null); // Reset active solution when changing tabs
  };

  const openSolution = (solutionName) => {
    setActiveSolution(solutionName);
  };

  useEffect(() => {
    if (postData_) {
      handleInputChange(postData_);
    }
  }, [postData_]);

  const getLibrary = (value) => {
    let path = {
      filePaths: [value],
    };
    setPath(value);
    postData("/v2/build_script", path);
  };
  useEffect(() => {
    getData("/v2/libraries_by_category");
  }, []);
  console.log(data);
  return (
    <div className="solution-repository-container">
      <h1>Explore and Apply Pre-Made Solutions</h1>
      <p>Access pre-made scripts for quick solutions</p>
      <div className="solution-repository-search-bar">
        <input type="text" placeholder="Search for scripts in the library..." />
      </div>
      <h2>Categories</h2>
      <div className="solution-repository-categories">
        <div
          className={`tab ${activeTab === "all" ? "active" : ""}`}
          onClick={() => openTab("all")}
        >
          All
        </div>
        <div
          className={`tab ${activeTab === "new" ? "active" : ""}`}
          onClick={() => openTab("new")}
        >
          New
        </div>
      </div>
      {data !== null ? (
        <>
          {" "}
          {activeTab === "all" && (
            <div className="solution-repository-solutions">
              {Object?.keys(data)?.map((category) => (
                <div key={category} className="solution-repository-solution">
                  <div
                    className="solution-box"
                    onClick={() => openSolution(category)}
                  >
                    <i
                      className={`fa ${
                        category === "User management" ? "fa-users" : "fa-cogs"
                      }`}
                      aria-hidden="true"
                    ></i>
                  </div>
                  <h3>{category}</h3>
                </div>
              ))}
            </div>
          )}
          {Object.keys(data)?.map(
            (category) =>
              activeSolution === category && (
                <div
                  key={category}
                  className="solution-repository-solution-view"
                >
                  <h2>{category}</h2>
                  <ul>
                    {data[category].map((solution) => (
                      <li key={solution.id}>
                        <i
                          className={`fa ${
                            solution?.scriptName?.includes("start")
                              ? "fa-play"
                              : "fa-file"
                          }`}
                          aria-hidden="true"
                        ></i>
                        <span
                          onClick={() => {
                            getLibrary(solution?.scriptPath);
                          }}
                        >
                          {solution.scriptName}
                        </span>
                      </li>
                    ))}
                  </ul>
                </div>
              )
          )}
        </>
      ) : (
        "Loading"
      )}
    </div>
  );
}

export default SolutionRepository;
