import Editor from "@monaco-editor/react";
import React, { useState } from "react";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import useFetch_POST from "../../services/http/Post";
import "./BuildSolution.css";
import SolutionRepository from "./SolutionRepository";

function CustomScript() {
  const navigate = useNavigate();
  const { isLoading, error, data, postData } = useFetch_POST();
  const problem = useSelector((state) => state.problem);

  const [script, setScript] = useState("");
  const [fileName, setFileName] = useState("");
  const [filePath, setfilePath] = useState("");

  const handleInputChange = (event) => {
    setScript(event);
  };
  const handleInputChange_ = (event) => {
    setScript(event.data);
  };
  const setPath = (data) => {
    setfilePath(data);
  };
  const clearNotepad = () => {
    setScript("");
  };

  const handleSave = () => {
    let data = {
      content: script,
      fileName: fileName,
    };
    postData(`/v2/save-script`, data);
  };
  const handleSaveSolution = () => {
    let data = {
      recommendation: "This is for server start",
      resolutionScript: filePath,
      problemTitle: problem.problemTitle,
      problemId: problem.problemId,
      serviceName: problem.serviceName,
    };
    postData(`/insert_remediation`, data);
  };

  if (data?.status == 200 || data?.status == 201) {
    navigate("/new-problem");
  }
  return (
    <div className="create-custom-script-container">
      <div className="create-custom-script-left-panel">
        <div className="custom-script">
          <h2>Create Custom Script</h2>
          <p>Write your own scripts from scratch</p>
          <div className="custom-script-editor">
            <Editor
              height="100%"
              defaultLanguage="bash"
              theme="vs-dark"
              value={script}
              onChange={handleInputChange}
            />
          </div>
          <div>
            <label name="fileName">File Name</label>
            <input
              type="text"
              value={fileName}
              onChange={(event) => {
                setFileName(event.target.value);
              }}
            />
          </div>
          <div className="custom-script-button">
            <button onClick={() => clearNotepad()}>Clear</button>
            <button onClick={handleSave}>Save</button>
            <button onClick={handleSaveSolution}>Save as Rule</button>
          </div>
          {filePath}
        </div>
      </div>
      <div className="create-custom-script-right-panel">
        <SolutionRepository
          handleInputChange={handleInputChange_}
          setPath={setPath}
        />
      </div>
    </div>
  );
}

export default CustomScript;
